import React from 'react';
import classNames from 'classnames/bind';
import PropTypes from 'prop-types';

import styles from './index.css';

const cx = classNames.bind(styles);

export const noDataEnum = {
    NOCONTENT: 'no_content', // 没有内容
    NONET: 'no_net', // 没有网络
    NOFOLLOW: 'no_follow', // 无跟踪事件
    NOSPILL: 'no_spill', // 无爆料事件
};

/**
 *
 * @param {bool} needCustom
 * @param {string} type
 * @param {any} children
 */

const NoData = ({ needCustom = false, type = noDataEnum.NOCONTENT, children = '暂无内容', style = {} }) => {
    const rootClass = cx('wrap', {
        [type]: !needCustom,
    });

    return (
        <div className={rootClass} style={{ ...style }}>
            <div className={styles.placeHolder} />
            <div className={styles.children}>{children}</div>
        </div>
    );
};

NoData.propTypes = {
    /**
     * 是否需要自定义图标类型，true需要， 默认为false不需要
     */
    needCustom: PropTypes.bool,
    /**
     * 图标类型枚举值为noDataEnum，可分为无网络，无内容，无跟踪事件，无爆料事件
     */
    type: PropTypes.string,
    /**
     * 传入的元素
     */
    children: PropTypes.any,
    style: PropTypes.any,
};

export default NoData;
