// 逻辑组件
const useItem = () => {
    // 展示status状态（-1待处理 0不采用 1采用）
    const showStatusText = status => {
        switch (status) {
            case -1:
                return '处理中';
            case 1:
                return '通过';
            case 0:
                return '未通过';
            default:
                break;
        }
    };

    return {
        showStatusText, // 展示对应的状态文本
    };
};

export { useItem };
