import React from 'react';
import errorBoundary from '@src/components/errorBoundary';
import PropTypes from 'prop-types';
import classnames from 'classnames/bind';
// 公共组件
// 图片组件
import ImgView from '@src/mobile/components/ImgView';

// 视频组件
import TimeListVideo from '@src/mobile/components/TimeListVideo';
// 逻辑组件
import { useItem } from './hooks/useItem';

import styles from './index.css';

const cx = classnames.bind(styles);

const Item = ({ data }) => {
    // 逻辑组件
    const { showStatusText } = useItem();

    // 父级数据
    const { content, createTime, video, img, mobile, reason, status, timeline } = data;
    const { title, label } = timeline;
    const imgUrl = img.url; // 图片
    const videoUrl = video.url; // 视频
    // console.log('createTime',createTime);

    return (
        <div className={styles.container}>
            {/* 顶部原因提示 */}
            {reason ? (
                <div
                    className={cx('reason', {
                        checked_reason: status === 1,
                        unchecked_reason: !status,
                    })}>
                    <span className={cx('text_reason')}>{reason}</span>
                </div>
            ) : null}
            {/* 内容 */}
            <div className={styles.itemWrapper}>
                {/* 日期/审核 */}
                <div className={styles.top}>
                    <div className={styles.date}>{createTime}</div>
                    {/*  展示status状态（-1审核中 0未通过 1已通过） */}
                    <div
                        className={cx('status', {
                            checked: status === 1,
                            checking: status === -1,
                            unchecked: !status,
                        })}
                    />
                </div>

                {/* 内容区域 */}
                <div className={styles.item}>
                    {/* 标题及icon  */}
                    <div className={styles.itemHeader}>
                        <div className={styles.title}>
                            <span className={styles.icon} />
                            <span>{title}</span>
                        </div>
                    </div>

                    <div className={styles.itemContentWraper}>
                        <div className={styles.itemContentFlex}>
                            <div className={styles.itemContenTitle}>爆料详情：</div>

                            <div className={styles.itemConten}>
                                {/* 爆料文案 */}
                                <div className={styles.content}>{content}</div>
                                {/* 图片组件 */}
                                <div className={styles.medias}>
                                    {imgUrl ? (
                                        <div>
                                            <ImgView img={imgUrl} alt="" />
                                        </div>
                                    ) : null}
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className={styles.itemContentVideoWrap}>
                        {/* 视频组件 */}
                        {video.url ? (
                            <TimeListVideo
                                videoConfig={{
                                    mobileUrl: video.url,
                                    duration: video.duration,
                                    bigPosterUrl: video.poster,
                                }}
                                // eslint-disable-next-line no-undef
                                // playermsg={{ height: '155px' }}
                            />
                        ) : null}
                    </div>

                    <div className={styles.itemContentfooter}>
                        <div className={styles.phone}>
                            联系方式：
                            <span>{mobile.replace(/^(\d{3})\d{4}(\d+)/, '$1****$2')}</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

Item.propTypes = {
    data: PropTypes.object,
};

export default errorBoundary(Item);
