import { useState, useEffect } from 'react';
import { Store } from '../../store';
// API接口
import { getMySpillList } from '../../service';
// 容器组件
const useList = () => {
    // 获取状态数据
    const store = Store.useContainer();
    const { states, changeStates } = store;
    const {
        // 全部列表
        nodes,
        // 目标数据
        parties, // 目标列表
        status, // 当前状态
        // 页数信息
        page,
        size,
        total,
    } = states;

    // 更新目标列表
    useEffect(
        () => {
            // 状态类型（默认全部/1采用/0不采用/-1待处理）
            changeStates(
                {
                    parties,
                },
                () => {},
            );
        },
        [status],
    );

    // 请求下一页
    const control = async () => {
        try {
            if ((page - 1) * size < total) {
                const res = await getMySpillList({ page, size, status });

                changeStates({
                    // 全部列表
                    nodes,
                    // 目标数据
                    parties: [...parties, ...res.nodes], // 目标列表(渲染首次UI列表)
                    // 页数信息
                    page: +page + 1,
                    size,
                    total,
                });

                return 'success';
            } else {
                console.log('无数据');

                return 'end';
            }
        } catch (e) {
            console.error(e);
        }
    };

    // 加载中

    return {
        nodes, // 总列表
        parties, // 目标列表
        status, // 当前状态
        page, // 当前页
        size, // 数量
        total, // 总数
        control,
    };
};

export { useList };
